/*
 * @Descripttion: 基础配置主页面js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2022-06-10 10:21:48
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 引入类型信息组件
import addBasicInfo from '@/views/main/BasicInfoConfiguration/addBasicInfo.vue';
import { searchCondetionFunc } from '@/utils/publicFunction';
import {
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE,
  BTN_TEXT,
  BASIC_INFO
} from '@/utils/constantsFile';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
// 导入所需接口
import {
  getPageBasicInformation,
  basicInformationDeleteByIdsAction,
  basicInformationUpdateByIdAction,
  basicInformationSelectAllBasicTypeAction,
  getListBasicInformationQueryParameter
} from '@/api/management';
export default {
  name: 'BasicInfoConfiguration',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    // 加载新增设备组件
    addBasicInfo,
    SearchBox,
    CustomColumn
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      BASIC_INFO: BASIC_INFO,
      // 设备修改表单数据
      formUpdate: {},
      // 搜索框有关
      isSearchBoxShow: false,
      searchList: [],
      searchData: [],
      // 设备修改表单验证
      rulesUpdate: {
        basicName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 200, message: '长度不得大于200个字符' }
        ],
        basicValue: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 200, message: '长度不得大于200个字符' }
        ],
        basicCode: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        basicExplanation: [{ max: 255, message: '长度不得大于255个字符' }]
      },
      // 编码下拉
      codeOptions: [],
      // table多选数据
      dataChecks: [],
      tableName: 'device',
      // 用于操作当前显示哪个组件 0：主页面 1：新增子节点参数
      showPage: 0,
      // 表格查询表单数据
      formParams: {
        model: null
      },
      // 模态框名称
      titlename: '',
      // 控制模态框修改
      dialogVisibleUpdate: false,
      // table加载动画
      tableLoading: false,
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        // {
        //   type: 'checkbox',
        //   width: '50',
        //   align: 'center'
        // },
        {
          field: 'basicName',
          title: BASIC_INFO.BASIC_NAME,
          align: 'center'
        },
        {
          field: 'basicValue',
          title: BASIC_INFO.BASIC_VALUE,
          align: 'center'
        },
        // {
        //   field: 'basicCode',
        //   title: BASIC_INFO.BASIC_CODE,
        //   align: 'center'
        // },
        {
          field: 'basicExplanation',
          title: BASIC_INFO.BASIC_EXPLANATION,
          align: 'center'
        }
      ],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false
    };
  },
  computed: {},
  created() {
    // 加载扩展字段
    this.setTableColume();
    // 调用设备列表查询
    this.findList();
    // this.getSearchList();
  },
  watch: {
    // 切换模态框显示和隐藏
    dialogVisibleUpdate(val) {
      if (val === false) {
        this.$refs.updateForm.resetFields();
      }
    }
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 操作下拉事件
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.setUpdate(row);
          break;
        case 'delete':
          this.setDelete(row);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 点击批量删除
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    deteleall() {
      if (this.dataChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.dataChecks.map((item) => arrdelete.push(item.basicId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => { });
      }
    },
    /**
     * @description: 执行批量删除的方法
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await basicInformationDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取批量勾选的数据
     * @param data // 勾选数据
     * @return: 无
     * @author: ZJM
     */
    tableChooseCheck(data) {
      this.dataChecks = data;
    },
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        const res = await getPageBasicInformation(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询全部编码
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findCodeList() {
      this.codeOptions = [];
      try {
        const res = await basicInformationSelectAllBasicTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.codeOptions = res.rows.map((item) => {
            var json = {};
            json.label = item.typeName;
            json.value = item.typeValue;
            return json;
          });
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 显示修改模态框
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setUpdate() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.findCodeList();
      this.dialogVisibleUpdate = true;
      this.formUpdate = JSON.parse(JSON.stringify(row));
    },
    /**
     * @description: 删除参数
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    setDelete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      // 确认是否删除
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.basicId]);
        })
        .catch(() => { });
    },
    addBasic() {
      this.showPage = 1;
    },
    /**
     * @description: 提交修改
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submitUpdate() {
      this.$refs.updateForm.validate((valid) => {
        // 若验证通过
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.basicId = this.formUpdate.basicId;
        form.basicName = this.formUpdate.basicName;
        form.basicValue = this.formUpdate.basicValue;
        form.basicCode = this.formUpdate.basicCode;
        form.basicExplanation = this.formUpdate.basicExplanation;
        const res = await basicInformationUpdateByIdAction(form);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
          this.findList();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回
     * @param val 0为显示index组件
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      // 显示组件
      this.showPage = val;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await getListBasicInformationQueryParameter({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(JSON.stringify(conditionArr1));
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[i].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(JSON.stringify(conditionArr2));
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[x].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description: 显示扩展字段列表头
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      // 设置恢复时的默认值
      for (var x = 0; x < this.tableColumns.length; x++) {
        this.tableColumns[x].zIndex = x;
        if (this.tableColumns[x].visible === undefined) {
          this.tableColumns[x].yVisible = true;
        } else {
          this.tableColumns[x].yVisible = this.tableColumns[x].visible;
        }
        if (this.tableColumns[x].width === undefined) {
          this.tableColumns[x].yWidth = 'auto';
        } else {
          this.tableColumns[x].yWidth = this.tableColumns[x].width;
        }
      }
    },
    /**
    * @description: 切换显示自定义列模态框
    * @param 无
    * @return: void
    * @author: ZJM
    */
    showCustomColumn() {
      this.dialogVisibleCustomColumn = !this.dialogVisibleCustomColumn;
    },
    /**
    * @description: 保存自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    saveCustomColumn(columns) {
      this.tableColumns = columns;
      this.showCustomColumn();
    },
    /**
    * @description: 恢复默认自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    resetCustomColumn() {
      this.showCustomColumn();
      // 按时间从大到小
      this.tableColumns = this.tableColumns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.tableColumns = this.tableColumns.map((item) => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    }
  }
};
