// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
import {
  basicInformationInsertBatchAction,
  basicInformationSelectAllBasicTypeAction
} from '@/api/management';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { TITLE, STATUS_CODE, MSG, VARIABLE, BTN_TEXT, BASIC_INFO } from '@/utils/constantsFile';
import { showErrorNotify } from '@/utils/notify';
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      imageUrl: '',
      // 页面标题
      titleName: BASIC_INFO.ADD_BASIC,
      // 表单验证
      validRules: {
        basicName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 200, message: '长度不得大于200个字符' }
        ],
        basicValue: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 200, message: '长度不得大于200个字符' }
        ],
        basicCode: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        basicExplanation: [{ max: 255, message: '长度不得大于255个字符' }]
      },
      // 可编辑配置
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: true
        // icon: 'fa fa-file-text-o'
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        // {
        //   field: 'basicName',
        //   title: BASIC_INFO.BASIC_NAME,
        //   align: 'center',
        //   editRender: { name: 'input' }
        // },
        {
          field: 'basicCode',
          title: BASIC_INFO.BASIC_NAME,
          align: 'center',
          editRender: {
            name: 'ElSelect',
            options: [],
            events: { change: this.ptypeChangeEvent }
          }
        },
        {
          field: 'basicValue',
          title: BASIC_INFO.BASIC_VALUE,
          align: 'center',
          editRender: { name: 'input' }
        },

        {
          field: 'basicExplanation',
          title: BASIC_INFO.BASIC_EXPLANATION,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <i
                  onClick={() => this.copyRow(row, column)}
                  class="icon-table-addRow"
                ></i>,
                <i
                  onClick={() => this.deleteRow(row, column)}
                  class="icon-table-delete"
                ></i>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() {
    this.findCodeList();
  },
  mounted() {
    // 默认新增一行
    this.insertEvent();
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      if (type === 'exit') {
        this.goBack();
        return;
      }
      this[`${type}`]();
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent() {
      const record = {
        basicCode: null
      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, -1);
    },
    async copyRow(row, column) {
      this.$refs.xGrid.clearActived();
      // const index = this.$refs.xGrid.$getRowIndex(row);
      const record = { ...row };
      delete record._XID;
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();

        const data = JSON.parse(JSON.stringify(insertRecords));
        const res = await basicInformationInsertBatchAction(
          {},
          data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 返回主页面
          this.goBack();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_FAILED}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 查询全部编码
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findCodeList() {
      this.codeOptions = [];
      try {
        const res = await basicInformationSelectAllBasicTypeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.codeOptions = res.rows.map((item) => {
            var json = {};
            json.label = item.typeName;
            json.value = item.typeValue;
            return json;
          });
          this.tableColumns[0].editRender.options = this.codeOptions;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    },
    ptypeChangeEvent(row, id, x) {
      for (x in this.codeOptions) {
        if (this.codeOptions[x].value === id) {
          row.row.basicName = this.codeOptions[x].label;
        }
      }
    }
  }
};
